export const isBrowser = () => typeof window !== "undefined"
const axios = require('axios');

export const getUser = () =>
  isBrowser() && window.localStorage.getItem("gatsbyUser")
    ? JSON.parse(window.localStorage.getItem("gatsbyUser"))
    : {}

const setUser = user =>
  window.localStorage.setItem("gatsbyUser", JSON.stringify(user))

  async function makePostRequest(email, password) {
    let user_api = 
    { 
      email: email, 
      password: password 
    };

    
    let res = await axios.post('http://localhost:3000/login', user_api,{withCredentials:true});
    
    // setUser({
    //   email: {email},
    //   password: {password}
    // })
    // console.log(getUser());
    let data = res.data;
    console.log("data:", data);
}

export const handleLogin = async ({ email, password}) => {
    // need to compare the inputs with database here
    if (email === `test@test.com` && password === `test2`) {
      setUser({
        username: `admin`,
        name: `Admin`,
        email: `test@test.com`,
      })
    }
    return await makePostRequest(email, password);

  //return false
}

export const isLoggedIn = () => {
  const user = getUser()

  return !!user.email
}

export const logout = callback => {
  setUser({})
  callback()
}
